import "../../resources/css/three-js-ui-group.css";

import React, {useState, forwardRef} from "react";
import {useDispatch, useSelector} from "react-redux";

import {setDisableHotkeys, setGroupColours} from "../../redux/threeDataSlice";

import {deselectLights, selectLightsByProperty} from "../Utility";

import UIColourPicker from "./UIColourPicker";

const UIGroup = forwardRef((props, ref) =>
{
    const dispatch = useDispatch();

    const [group, setGroup] = useState("");
    const [currGroup, setCurrGroup] = useState("");
    const groupColours = useSelector((state) => state.groupColours.value);
    const [colour, setColour] = useState("#FFFFFF");
    const [showPicker, setShowPicker] = useState(false);
    const [showColour, setShowColour] = useState(false);

    function handleChange(e)
    {
        setGroup(e.target.value);
    }

    function handleSelect()
    {
        deselectLights();
        setCurrGroup(group);
        selectLightsByProperty("group", group);

        if (group in groupColours)
            setColour(groupColours[group]);
        else
            setColour("#808080");

        setShowColour(true);
    }

    function handleColour()
    {
        setShowPicker(showPicker => !showPicker);
    }

    function handleCancel()
    {
        setShowPicker(false);
        
    }

    function handleConfirm(colour)
    {
        setShowPicker(false);
        setColour(colour);
        var obj = {...groupColours};
        obj[currGroup] = colour;
        dispatch(setGroupColours(obj));
    }

    function focus()
    {
        dispatch(setDisableHotkeys(true));
    }

    function blur()
    {
        dispatch(setDisableHotkeys(false));
    }

    return(
        <div className="three-ui-box" id="group">
            <div className="block" id="group-select">
                <div className="label">Group ID:</div>
                {/* text input */}
                <input
                    ref={ref}
                    className="group-select"
                    type="text"
                    name="group-select"
                    value={group}
                    placeholder="Enter group ID"
                    onChange={handleChange}
                    onFocus={focus}
                    onBlur={blur}
                /> 
                <div
                    className="colour-display"
                    style={{backgroundColor: colour, 
                            visibility: showColour ? "visible" : "hidden"}}
                >
                    &nbsp;
                </div>
                {/* colour picker */}
                {showPicker && 
                    <UIColourPicker
                        colour={colour}
                        setColour={setColour}
                        cancel={handleCancel}
                        confirm={handleConfirm}
                    />
                }
                {/* buttons */}
                <div 
                    className="three-btn colour"
                    id={showColour ? "" : "disabled"}
                    onClick={handleColour}
                    style={{backgroundColor: "#7F849F"}}
                >
                    COLOUR
                </div>
                <div 
                    className="three-btn select" 
                    id={group !== "" ? "" : "disabled"}
                    onClick={handleSelect}
                    style={{backgroundColor: "#7F849F"}}
                >
                    SELECT
                </div>
            </div>
        </div>
    );
});

export default UIGroup;